body {
  background-color: #e8ecf4;
}
.landing {
  height: 100vh;
}
.App {
  font-family: Inter;
  text-align: center;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  margin: 6%;
  border: 2px solid blue;
}
.App > img {
  margin-top: 3%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  height: 60px;
  width: 100px;
  margin-bottom: 2%;
}
.userform {
  display: flex;
}
.form {
  display: flex;
  flex-direction: column;
  margin: auto;
}
.userForm-container {
  width: 50%;
  margin: auto;
  max-width: 400px;
}
label {
  text-align: left;
}
