span {
  font-weight: lighter;
  color: grey;
}
.card-container {
  display: flex;
  justify-content: space-between;
}

.card-input-element {
  display: none;
}

.card-input {
  margin: 10px;
  padding: 5px;
  border: 1px solid lightgray;
  border-radius: 5%;
  justify-content: space-between;
  margin: 5px;
}

.card-input:hover {
  cursor: pointer;
}
.card-input-element:checked + .card-input {
  box-shadow: 0 0 1px 1px #664de5;
}
.panel-heading {
  font-size: 15px;
}
.panel-body {
  font-size: 12px;
  font-weight: lighter;
}

.slct {
  appearance: none;
  outline: 0;
  background: green;
  background-image: none;
  width: 100%;
  height: 100%;
  color: black;
  cursor: pointer;
  border: 1px solid black;
  border-radius: 3px;
}

.selectt {
  position: relative;
  display: block;
  width: 15em;
  height: 2em;
  line-height: 3;
  overflow: hidden;
  border-radius: 0.25em;
  padding-bottom: 10px;
}
